<script>
  import { onMount } from 'svelte';
  import { fade } from 'svelte/transition';

  import Router from 'svelte-standalone-router';
  
  // sections
  import Header from '~/components/header.svelte';
  import Footer from '~/components/footer.svelte';
  
  import { lang, theme } from '~/stores/application';

  // show scroll to top button
  let topOfPage = false;
  document.addEventListener('scroll', (e) => {
    if((window.pageYOffset || document.scrollTop)  - (document.clientTop || 0) > 100){
      topOfPage = true;
    }else{
      topOfPage = false;
    }
  });

  // make sure transition plays on first mount as well
  let show;
  onMount(_ => {
    show = true;
  });

</script>

<div id="app">
  
  <h1 class="branding">Jens Hjalmarsson</h1>
  <h2>Webbutvecklare, Webbdesigner, Programmerare och mer.</h2>
  
  <Header sticky={topOfPage} />
  
  <main>
    <Router let:component let:props>
      {#if show}
        {#key component}
          <div id="router" in:fade={{ duration: 800, delay: 150 }}>
            <svelte:component this={component} {...props} />
          </div>
        {/key}
      {/if}
    </Router>
  </main>
  
  <Footer />

  <button class="top-of-page" class:show={topOfPage} on:click={_ => window.scroll({top: 0, behavior: 'smooth'})}>
    {$lang.top}
    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24"><path d="M0 16.67l2.829 2.83 9.175-9.339 9.167 9.339 2.829-2.83-11.996-12.17z"/></svg>
  </button>
</div>


<style>
  h1, h2{
    display: none;
  }
</style>