<script>
  export let change = (e) => {
    console.log('Not implemented');
  };
  export let checked = false;
</script>

<div class="onoffswitch">
  <input on:change={change} type="checkbox" name="onoffswitch" class="onoffswitch-checkbox" id="myonoffswitch" tabindex="0" checked={checked}>
  <label class="onoffswitch-label" for="myonoffswitch"></label>
</div>


<style>
  .onoffswitch {
    position: relative; 
    width: 55px;
    -webkit-user-select:none; 
    -moz-user-select:none; -ms-user-select: none;
  }
  .onoffswitch-checkbox {
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }
  .onoffswitch-label {
    display: block; 
    overflow: hidden; 
    cursor: pointer;
    height: 20px; 
    padding: 0; 
    line-height: 20px;
    border: 0px solid var(--accent-color); 
    border-radius: 30px;
    background-color: var(--color);
  }
  .onoffswitch-label:before {
    content: "";
    display: block; 
    width: 30px; 
    margin: -5px;
    background: #fff;
    position: absolute; 
    top: 0; 
    bottom: 0;
    left: 0;
    transform: translateX(31px);
    border-radius: 30px;
    box-shadow: 0 6px 12px 0px rgba(0, 0, 0, 0.2);
    transition: transform .6s;
  }
  .onoffswitch-checkbox:checked + .onoffswitch-label {
    background-color: var(--color);
  }
  .onoffswitch-checkbox:checked + .onoffswitch-label, .onoffswitch-checkbox:checked + .onoffswitch-label:before {
    border-color: var(--color);
  }
  :global(.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner){
    margin-left: 0;
  }
  .onoffswitch-checkbox:checked + .onoffswitch-label:before {
    transform: translateX(0px);
    background-color: var(--accent-color); 
    box-shadow: 3px 6px 18px 0px rgba(0, 0, 0, 0.2);
  }
</style>